.navbar {
    height: 80vh;
}

.navbarLink {
    /* border: 1px solid black; */
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 30px;
    width: 260px;
    height: 80vh;
    background-color: #C68FE6;
    border-right: 3px solid white;
    border-radius: 10px 0px 0px 10px;
    gap: 60px;
    
}

.headLink {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    background-color: #fd835e;
    color: white;
    width: 200px;
    font-weight: 600;
    border-radius: 10px;
    padding: 10px;
    gap: 10px;
    transition: border 0.3s linear;
}

.headLink:hover {
    border: 2px solid white;
}

@media (max-width:480px) {
    .navbar {
        height: 12vh;
        width: 100%;
    }

    .navbarLink {
        height: 12vh;
        width: 100%;
        font-size: 12px;
        font-weight: 600;
        border-radius: 10px 10px 0px 0px;
        border-bottom: 3px solid white;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }
 
    #navLogo1,
    #navLogo2,
    #navLogo3 {
        font-size: 20px;
    }
}